*{
  box-sizing: border-box; 
  padding: 0;
  margin: 0;
}
body{
  background-color:#ebd5d5 ;
  font-family: 'Abel', sans-serif;
  
}
h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif;
}

.navbar{
  background-color: #685454;
  color: white;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  min-height: 10vh; 

}

.footer{
  
    background-color: #685454;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 10vh; 
  
}

.book-list{
  min-height: 80vh;
  display: grid; 
  grid-template-columns: repeat(3,1fr);
  gap: 20px; 
  justify-items: center;

  
}

.book{
  text-align: center;
  padding: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Distribute space between elements */
  background-color: #f0f0f0;
  padding: 15px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  height: 100%; /* Ensure the item takes up the full height of its grid cell */
  border-radius: 5%;
  margin: 2vh;
  min-width: 400px;
  border: solid 2px #685454;
  
}

