.book-item.title{
margin-bottom: 0;
min-height: 8vh; 
font-size: 17px;
line-height: 0.85;
padding: 5px 20px;
  /* overflow: hidden; 
  text-overflow: ellipsis; 
  white-space: normal;  */
}



img {
  width: 200px;
  height: 300px;
  border-radius: 5%;
  border: solid 5px #685454;
  margin-bottom: 5px;
  transition: all 0.3s ease; 
 }
img:hover{
  transform: scale(1.1)
}
 h2{
  margin: 5px auto;
 }
 button{
  padding: 1px 10px;
  font-family: 'Playfair Display', serif;
  font-size: 20px;
  border-radius: 7%;
  background-color: #ebd5d5;
  transition: all  0.3s  ease;
 }

 button:hover{
  transform:scale(1.05);
 }

 .navbar h3{
  font-size: 30px;
  color: white;
  text-decoration: none;
 }

 .navbar h1:hover,
.navbar h3:hover {
  transform: scale(1.1);
  text-decoration:underline ;

}

 a{
  text-decoration: none;
  color: white;
  transition: all  0.3s  ease;
 }


 .back-btn{
  display: flex;
  align-items: flex-end;
  margin: 10px 10px;
  margin-bottom: 0px;
  border: none;
  line-height: 1.2;
  text-decoration: underline;
  transition: all  0.3s  ease;
 }

 .back-btn:hover{
  transform: scale(1.1);
 }

 .favorites{
  min-height: 60vh;
  /* background-color: red; */


 }
 .title{
  display: flex;
  justify-content: center;
  scale: 1.2;
 
}


.fav-list{
    display: grid; 
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
    justify-items: center;
    padding-bottom: 4vh;
    /* min-height: 50vh; */
    /* background-color: red; */


}
.favorites .book-item img{
margin :0;
padding: 0;
min-height: 0vh;

/* background-color: black; */
}
.favorites .book{
  min-width: 400px;
  /* background-color: black; */
}


.fav-list .book-item.title{
  margin:0;
  font-size: 20px;
  min-height: 0vh;
}
.detailed-book-intro{
  
  padding: 1vh;
  display: block;
  flex-direction: column;
  justify-content: space-between; 
  background-color:#f0f0f0;
}
.detailed-book-intro h1{
  line-height: 1;
 width: 19vw;
 margin:30px auto;
}
.detailed-container{
  display: flex;
  flex-direction: row;
  justify-content:center;
  background-color: #f0f0f0;
  padding: 5vh;
  max-width:80vw;
 align-items: center;
 border-radius: 5%;
 border: solid 5px #685454;

 /* center aligns container itself */
  margin: 20px auto; 
  margin-bottom: 40px;
 
}

.detailed-book-intro img{

  width: fit-content;
  scale:1.05;
 
}
.book-details p {
  font-size: 19px;
}
.all-book-info{
  margin: auto 10px;
  word-spacing: 3px;
  
}

.all-book-info .item{
  margin: 10px auto;
}
.item .rating{
  display: flex;
align-items: flex-end;
  
}
.item .rating svg{ 
 margin-left: 10px;
}